/* App.css - Enhanced, Responsive, Attractive */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  color: #333;
  line-height: 1.6;
}

.App {
  min-height: 100vh;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 5%;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
  flex-wrap: wrap;
  gap: 1rem;
}

.logo-section {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  object-fit: cover;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.site-name {
  font-size: 1.6rem;
  font-weight: 700;
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.middle-section {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 0.9rem;
  color: #555;
  flex-wrap: wrap;
  justify-content: center;
}

.middle-section img {
  width: 150px;
  height: 100px;
}

.text-group p {
  margin: 2px 0;
}

.welcome-text {
  font-weight: 600;
  color: #444;
  font-size: 30px;
}

.tagline, .extra-text {
  font-size: 0.8rem;
  color: #777;
}

.auth-buttons {
  display: flex;
  gap: 12px;
}

.signin, .signup {
  padding: 0.6rem 1.4rem;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.signin {
  background: #f8f9fa;
  color: #333;
  border: 2px solid #e0e0e0;
}

.signin:hover {
  background: #e9ecef;
  border-color: #2575fc;
  color: #2575fc;
}

.signup {
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  color: white;
  box-shadow: 0 4px 15px rgba(37, 117, 252, 0.3);
}

.signup:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(37, 117, 252, 0.4);
}

/* Main */
.main {
  padding: 2rem 5%;
  max-width: 1400px;
  margin: 0 auto;
}

/* Daily Update */
.daily-update {
  text-align: center;
  margin-bottom: 3rem;
  padding: 2rem;
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
}

.daily-update h2 {
  font-size: 2.2rem;
  background: linear-gradient(90deg, #2575fc, #6a11cb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 1rem;
}

.daily-update p {
  font-size: 1.1rem;
  color: #555;
  max-width: 800px;
  margin: 0 auto;
}

/* Four Columns */
.four-columns {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.column {
  background: white;
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.column:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.12);
}

.column img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.column h3 {
  padding: 1rem 1.5rem 0.5rem;
  font-size: 1.3rem;
  color: #222;
}

.column p {
  padding: 0 1.5rem 1.5rem;
  color: #666;
  font-size: 0.95rem;
}

/* Three Columns */
.three-columns {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.three-columns img,
.three-columns video {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-radius: 16px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

.three-columns video {
  background: #000;
}

/* Tuition Section */
.tuition-section {
  background: white;
  padding: 3rem;
  border-radius: 24px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.tuition-section h2 {
  text-align: center;
  font-size: 2.3rem;
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0.8rem;
}

.tuition-section > p {
  text-align: center;
  color: #555;
  font-size: 1.1rem;
  max-width: 800px;
  margin: 0 auto 2.5rem;
}

/* Subject Grid */
.subject-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.8rem;
  margin-bottom: 2.5rem;
}

.subject-card {
  background: #fff;
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.subject-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.12);
}

.subject-card.active {
  border-color: #6a11cb;
  box-shadow: 0 0 0 4px rgba(106, 17, 203, 0.2);
  transform: scale(1.02);
}

.subject-img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.subject-card:hover .subject-img {
  transform: scale(1.08);
}

.subject-card h3 {
  padding: 1rem 1.2rem 0.5rem;
  font-size: 1.4rem;
  color: #222;
  text-align: center;
}

.subject-card > p {
  padding: 0 1.2rem 1rem;
  color: #666;
  font-size: 0.95rem;
  text-align: center;
}

/* Class Buttons */
.class-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
  padding: 0 1rem 1rem;
  justify-content: center;
}

.class-btn {
  padding: 0.55rem 1rem;
  font-size: 0.85rem;
  border: 2px solid #e0e0e0;
  background: #f9f9f9;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
}

.class-btn:hover {
  background: #f0f0f0;
  border-color: #2575fc;
  color: #2575fc;
}

.class-btn.selected {
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  color: white;
  border-color: transparent;
  font-weight: 600;
}

/* Steps */
.step {
  margin-top: 2rem;
  padding: 2rem;
  background: linear-gradient(135deg, #f8f9ff, #e6f0ff);
  border-radius: 16px;
  text-align: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

.step h3 {
  font-size: 1.6rem;
  color: #222;
  margin-bottom: 1rem;
}

.step h4 {
  font-size: 1.3rem;
  color: #444;
  margin-bottom: 1rem;
}

.step input[type="date"] {
  padding: 0.9rem 1.2rem;
  font-size: 1.1rem;
  border: 2px solid #ddd;
  border-radius: 12px;
  width: 100%;
  max-width: 300px;
  transition: all 0.3s ease;
}

.step input[type="date"]:focus {
  outline: none;
  border-color: #6a11cb;
  box-shadow: 0 0 0 4px rgba(106, 17, 203, 0.15);
}

/* Time Slots */
.time-slots {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin-top: 1rem;
}

.time-btn {
  padding: 0.8rem 1.6rem;
  font-size: 1rem;
  font-weight: 600;
  border: 2px solid #e0e0e0;
  background: white;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.time-btn:hover {
  background: #f5f5f5;
  border-color: #2575fc;
  color: #2575fc;
}

.time-btn.active {
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  color: white;
  border-color: transparent;
  transform: scale(1.05);
}

/* Payment Button */
.payment-btn {
  display: block;
  margin: 2.5rem auto 0;
  padding: 1rem 3rem;
  font-size: 1.2rem;
  font-weight: 700;
  color: white;
  background: linear-gradient(90deg, #11998e, #38ef7d);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0 8px 25px rgba(17, 153, 142, 0.3);
  transition: all 0.3s ease;
}

.payment-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 30px rgba(17, 153, 142, 0.4);
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .header {
    flex-direction: column;
    text-align: center;
    padding: 1rem 3%;
  }

  .middle-section {
    order: 3;
    margin-top: 1rem;
  }

  .four-columns,
  .subject-grid {
    grid-template-columns: 1fr;
  }

  .three-columns {
    grid-template-columns: 1fr;
  }

  .tuition-section {
    padding: 2rem;
  }

  .daily-update h2,
  .tuition-section h2 {
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .class-buttons {
    gap: 0.5rem;
  }

  .class-btn {
    font-size: 0.8rem;
    padding: 0.5rem 0.9rem;
  }

  .time-btn {
    padding: 0.7rem 1.2rem;
    font-size: 0.9rem;
  }
}
